.Post__heart-animation-icon {
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 50px;
  color: rgba(255, 255, 255, 0.8);
  -webkit-animation: heart 500ms ease-in-out;
          animation: heart 500ms ease-in-out;
}

@-webkit-keyframes heart {
  0% { font-size: 40px; opacity: 0; z-index: 0; }
  50% { font-size: 80px; opacity: 0.8; z-index: 1; }
  100% { font-size: 80px; opacity: 0.8; z-index: 1; }
}

@keyframes heart {
  0% { font-size: 40px; opacity: 0; z-index: 0; }
  50% { font-size: 80px; opacity: 0.8; z-index: 1; }
  100% { font-size: 80px; opacity: 0.8; z-index: 1; }
}


